body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.searchbar{
  width: 200px;
  margin: 20px;
  font-size: 13px;
}

.course_code{
  text-align: center;
}

.description{
  width: 600px;
}

.instruction{
  text-align: center;
}

.graph_container{
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reset_zoom_btn{
  border: 1px solid #2c2c2c;
  border-radius: 3px;
  color: #2c2c2c;
  font-size: 12px;
}

.reset_zoom_btn:hover{
  background-color: #2c2c2c;
  color: white;
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  cursor: pointer;
}

.reset_zoom_btn:focus{
  outline:0;
}

